<template>
  <div id="passRules">
    <div class="contantHeader">

      <span class="title">{{$t('accessSystem.gaojing')}}</span>
      <span style="font-size:14px;margin-left:1.7rem">{{$t('accessSystem.gaojingshijian')}}：</span>
      <!-- <el-date-picker
        v-model="dateTime"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        :picker-options="pickerOptions2"
        @change="consolechange"
        value-format='yyyy-MM-dd'
        
      >
      </el-date-picker>
      <el-time-picker
        is-range
        arrow-control
        v-model="certainTime"
        range-separator="至"
        start-placeholder="开始时间"
        end-placeholder="结束时间"
        placeholder="选择时间范围"
      > -->
      <el-date-picker
        v-model="dateTime"
        type="datetimerange"
        align="right"
        unlink-panels
         :clearable='false'
        :start-placeholder="$t('accessSystem.kaishiriqi')"
        :end-placeholder="$t('accessSystem.jieshuriqi')"
        :default-time="['12:00:00', '08:00:00']"
        value-format='yyyy-MM-dd HH:mm:ss'
        @change="consolechange"
      >
      </el-date-picker>
      <span style="font-size:14px;margin-left:0.32rem">{{$t('accessSystem.gaojingleixing')}}：</span>

      <el-select
        v-model="searchType"
        :placeholder="$t('accessSystem.qingxuanze')"
        clearable
      >

        <el-option
          v-for="item in typeList"
          :key="item.name"
          :label="item.name"
          :value="item.name"
        >
        </el-option>
      </el-select>
      <el-input
        :placeholder="$t('accessSystem.qingshurugaojingdidian')"
        v-model="deviceAddress"
        clearable
        width='100px'
        maxlength="20"
      >

      </el-input>
      <el-button
        round
        type="primary"
        class="btn"
        @click="getAlertList"
      >{{$t('accessSystem.chaxun')}}</el-button>
      <el-button
        round
        @click="deleteAlert"
      >{{$t('accessSystem.piliangshanchu')}}</el-button>
    </div>
    <!-- 详情 -->
    <el-dialog
      :title="$t('accessSystem.xiangqing')"
      :visible.sync="detailsDialog"
      width="35%"
      :before-close="detailsDialogClose"
    >

      <el-row>
        <el-col
          :span="8"
          v-if="detailInfo.type!='陌生人闯入告警'"
        >
          <div class="grid-contentFir"> <img
              :src="detailInfo.urls"
              class="snapduibi"
              alt='暂无'
            ></div>
        </el-col>
        <el-col
          :span="8"
          style="margin-top:100px"
          v-if="detailInfo.type!='陌生人闯入告警'"
        >
          <div class="grid-contentFir"><span class="score">{{detailInfo.score*100+'%'}}</span></div>
        </el-col>
        <el-col :span="8">
          <div class="grid-contentFir"> <img
              :src="detailInfo.url"
              class="snapduibi"
               alt='暂无'
            ></div>
        </el-col>

      </el-row>
      <el-row>
        <div class="grid-contentSec">{{$t('accessSystem.gaojingleixing')}}:<label class="value">{{detailInfo.type}}</label></div>
        <div
          class="grid-contentSec"
          v-if="detailInfo.type!='陌生人闯入告警'"
        >{{$t('accessSystem.xingming')}}:<label class="value">{{detailInfo.personName}}</label></div>
        <div
          class="grid-contentSec"
          v-if="detailInfo.type!='陌生人闯入告警'"
        >{{$t('accessSystem.shenfenzhenghao')}}:<label class="value">{{detailInfo.code}}</label></div>
        <!-- <div  class="grid-content ">性别:<label class="value"></label></div>  -->
        <div class="grid-contentSec">{{$t('accessSystem.shijian')}}:<label class="value">{{detailInfo.time}}</label></div>
        <div class="grid-contentSec">{{$t('accessSystem.weizhi')}}:<label class="value">{{detailInfo.address}}</label></div>

        <!-- <el-col :span="12" v-for="(item,index) in tableDataLabel" :key="index"><div  class="grid-content ">{{item.name}}:<label class="value">{{detailsShow[item.property]}}</label></div></el-col> -->

      </el-row>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="detailsDialog = false">{{$t('accessSystem.guanbi')}}</el-button>
      </span>
    </el-dialog>
    <!--  -->
    <div class="contantTable">
      <el-table
        :data="alertData"
        style="width: 100%"
        :row-key="getRowKeys"
        @selection-change="handleSelectionChange"
      >
        <el-table-column
          type="selection"
          width="55"
          :reserve-selection="true"
        >
        </el-table-column>
        <el-table-column
          prop="id"
          :label="$t('accessSystem.xuhao')"
        >
        </el-table-column>
        <el-table-column
          prop="type"
          :label="$t('accessSystem.gaojingleixing')"
        >
        </el-table-column>
        <el-table-column
          prop="time"
          :label="$t('accessSystem.gaojingshijian')"
        >
        </el-table-column>
        <el-table-column
          prop="address"
          :label="$t('accessSystem.gaojingdidian')"
        >
        </el-table-column>
        <el-table-column :label="$t('accessSystem.zhuapaizhaopian')">
          <template slot-scope="imgScope">
            <img
              :src="imgScope.row.url"
              alt=""
            >

          </template>
        </el-table-column>
        <el-table-column
          prop="person.name"
          :label="$t('accessSystem.xingming')"
        >
        </el-table-column>
        <el-table-column :label="$t('accessSystem.caozuo')">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="text"
              @click="getAlertDetail(scope.row)"
            >{{$t('accessSystem.xiangqing')}}</el-button>
            <!-- @click="handleEdit(scope.$index, scope.row)" -->

            <el-button
              size="mini"
              type="text"
              @click="deleteAlert(scope.row)"
            >{{$t('accessSystem.shanchu')}}</el-button>
            <!-- @click="handleDelete(scope.$index, scope.row)" -->
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        class="pagination"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNumber"
        :page-sizes="[5, 10, 20, 40]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
  </div>
</template>
<script>
import APICONFIG from "../../../../../../config/api.js";
export default {
  data() {
    return {
      detailsDialog: false,
      pickerOptions2: {
        shortcuts: [
          {
            text: this.$t('accessSystem.zuijinyizhou'),
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: this.$t('accessSystem.zuijinyigeyue'),
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: this.$t('accessSystem.zuijinsangeyue'),
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      value: "",
      value5: "",
      certainTime: "",
      dateTime: "",
      tableData: [],
      alertData: [],
      typeList: [],
      searchType: "",
      deviceAddress: "",
      alertIds: [],
      multipleSelection: [],
      pageSize: 5,
      total: 1,
      pageNumber: 1,
      detailInfo: {},
      getRowKeys(row) {
        return row.id;
      }
    };
  },
  created() {
    //格式化当前时间
    Date.prototype.Format = function(fmt) {
      //author: meizz
      var o = {
        "M+": this.getMonth() + 1, //月份
        "d+": this.getDate(), //日
        "h+": this.getHours(), //小时
        "m+": this.getMinutes(), //分
        "s+": this.getSeconds(), //秒
        "q+": Math.floor((this.getMonth() + 3) / 3), //季度
        S: this.getMilliseconds() //毫秒
      };
      if (/(y+)/.test(fmt))
        fmt = fmt.replace(
          RegExp.$1,
          (this.getFullYear() + "").substr(4 - RegExp.$1.length)
        );
      for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt))
          fmt = fmt.replace(
            RegExp.$1,
            RegExp.$1.length == 1
              ? o[k]
              : ("00" + o[k]).substr(("" + o[k]).length)
          );
      return fmt;
    };
    this.dateTime = [
      new Date().Format("yyyy-MM-dd 00:00:00"),
      new Date().Format("yyyy-MM-dd 23:59:59")
    ];
    console.log(this.dateTime);
    //
    this.netAPI = APICONFIG().get("accessSystem");
    this.getAlertList();
    this.getAlertType();
  },
  methods: {
    getAlertDetail(row) {
      this.detailsDialog = true;
      this.detailInfo = row;
      if (row.type == "黑名单闯入告警") {
        console.log("查询人员");
        this.$http
          .get(this.netAPI.alert.peopleDetail, {
            params: { id: row.person.id }
          })
          .then(res => {
            if (res.body.status == 200) {
              console.log(res.body.data.code, res.body.data.name);
              this.$set(this.detailInfo, "code", res.body.data.code);
              this.$set(this.detailInfo, "personName", res.body.data.name);
              this.$set(this.detailInfo, "urls", res.body.data.urls[0]);
              this.$set(this.detailInfo, "code", res.body.data.code);

              console.log(this.detailInfo);
            } else {
              this.$message.error(res.body.message);
            }
          });
      }

      console.log(this.detailInfo);
    },
    detailsDialogClose() {
      this.detailInfo = {};
      this.detailsDialog = false;
    },
    handleSizeChange(pagesize) {
      this.pagesize = pagesize;
      this.getAlertList();
    },
    handleCurrentChange(pageNumber) {
      this.pageNumber = pageNumber;
      this.getAlertList();
    },
    //多选数据收集
    handleSelectionChange(val) {
      console.log(val);
      if (val.length) {
        this.multipleSelection = val;
      } else {
        this.multipleSelection = [];
      }
    },
    deleteAlert(row) {
      if (!row.id && this.multipleSelection.length < 1) {
        this.$message.warning(this.$t('accessSystem.qingxuanzeshanchuxiang'));
      } else {
        
        if (row.id) {
          this.alertIds = [{ id: row.id }];
         
        } else {
          for (const key in this.multipleSelection) {
            if (this.multipleSelection.hasOwnProperty(key)) {
              const element = this.multipleSelection[key];
              this.alertIds.push({id:element.id});
            }
          }
        }
        console.log(this.alertIds);
        this.$http
          .post(this.netAPI.alert.alertRemove, this.alertIds)
          .then(res => {
            if (res.body.status == 200) {
              this.$message.success(this.$t('accessSystem.shanchuchenggong'));
              if (this.alertData.length == 1) {
                this.pageNumber =
                  this.pageNumber - 1 > 0 ? this.pageNumber - 1 : 1;
              }
              this.getAlertList();
            } else {
              this.$message.error(res.body.message);
            }
          });
      }
    },
    //告警类型查询
    getAlertType() {
      this.$http.get(this.netAPI.alert.alertType).then(res => {
        if (res.body.status == 200) {
          this.typeList = res.body.data;
        } else {
          this.$message.error(res.body.message);
        }
      });
    },
    consolechange() {
      console.log(this.dateTime);
    },
    getAlertList() {
      if(this.dateTime ==null){
         var params = {
       
        types: this.searchType,
        address: this.deviceAddress,
        pageNo: this.pageNumber,
        pageSize: this.pageSize,
        sort: "id",
        order: "asc"
      };
      }else{
        var params = {
        startTime: this.dateTime[0],
        endTime: this.dateTime[1],
        types: this.searchType,
        address: this.deviceAddress,
        pageNo: this.pageNumber,
        pageSize: this.pageSize,
        sort: "id",
        order: "asc"
      };
      }
      
      this.$http
        .get(this.netAPI.alert.alertList, { params: params })
        .then(res => {
          if (res.body.status == 200) {
            this.alertData = res.body.data;
            this.total = res.body.total;
          } else {
            this.$message.error(res.body.message);
          }
        });
    }
  }
};
</script>
<style scoped>
#passRules {
  height: 800px;
  background-color: white;
  margin: 0 -0.96rem;
}
.contantHeader {
  font-size: 20px;
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  color: rgba(39, 39, 39, 1);
  padding-top: 23px;
}
.title {
  margin-left: 32px;
}
.btn {
  box-shadow: 0px 5px 6px 0px rgba(179, 209, 241, 1);
}
.contantTable {
  padding-top: 20px;
}
.pagination {
  float: right;
  margin-top: 25px;
  margin-right: 0.62rem;
}
.el-input {
  width: auto;
}
.score {
  color: red;
  font-size: 20px;
  margin-top: 100px;
}
.snapduibi {
  width: 160px;
  height: 240px;
  display: inline-block;
  margin: 0 auto;
}
.grid-contentFir{
   text-align: center;
  margin-top: 10px;
}
.grid-contentSec {
  text-align: left;
  margin-top: 10px;
}
.imgpreviewL {
  width: 300px;
}
.value {
  margin-left: 10px;
}
</style>



